




















import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class EmployeesHeader extends Vue {
  private search = '';

  private emitSearch(value: string) {
    this.search = value;
    this.$emit('search', this.search);
  }

  private newEmployee() {
    this.$emit('new-employee');
  }
}
